.loader {
  & .spinner-border {
    height: 100px;
    width: 100px;

    top: 200px;
    bottom: 0;
    margin: 1% auto;
    position: absolute;
    left: 0;
    right: 0;
  }
}
.pagination {
  display: flex;
  flex-wrap: wrap;
}
.verde {
  color: green;
}
.rojo {
  color: red;
}
.float {
  position: fixed;
  width: 50px;
  height: 30px;
  bottom: 40px;
  right: 5px;
  top: -11px;
}
